@import 'imports/normalize';
@import 'imports/mixins';

/*
>>================================================================================>
Site Variables
>>================================================================================>
*/

/*
Font Faces
>>------------->
*/

$font_primary: roc-grotesk, sans-serif;
$font_secondary: roc-grotesk-wide, sans-serif;

/*
Font Weights
>>------------->
*/

$weight_light: 300;
$weight_regular: 400;
$weight_medium: 500;
$weight_semibold: 600;
$weight_bold: 700;
$weight_wide-bold: 600;
$weight_black: 900;

/*
Colors
>>------------->
*/

$color_white: #ffffff;
$color_black: #000000;
$color_light-purple: #AA00FF;
$color_yellow: #FFEBB6;

/*
Site Colors
>>------------->
*/

$color_background: $color_light-purple;
$color_highlighted_text: $color_white;
$color_highlighted_text_background: $color_black;

/*
Breakpoints
>>------------->
*/

$breakpoint_desktop: 768px;
$breakpoint_mobile: ($breakpoint_desktop - 1);

/*
Dimensions
>>------------->
*/

$site_width: 1200px;
$padding_desktop: 30px;
$padding_mobile: 25px;
$spacing_desktop: 60px;
$spacing_mobile: 60px;
$gutter_normal: 50px;
$gutter_small: 20px;

/*
>>================================================================================>
Additional Resets and Helpers
>>================================================================================>
*/

@import 'imports/resets-helpers';

.sprite {
    position: absolute;
}

/*
>>================================================================================>
Global: Framework
>>================================================================================>
*/

/*
All Resolutions
>>------------->
*/

body {
    background-color: $color_light-purple;
}

main {
    position: relative;
    overflow: hidden;
}

/*
Desktop
>>------------->
*/

@media screen and (min-width: 768px) {
    .container {
        padding: 0 $padding_desktop;
    }
}

/*
Mobile
>>------------->
*/


@media screen and (max-width: 767px) {
    .container {
        padding: 0 $padding_mobile;
    }
}

/*
>>================================================================================>
Global: Reusable Styles
>>================================================================================>
*/

/*
All Resolutions
>>------------->
*/

@keyframes hypno-eye-scale {
    0% {
        transform: scale(0) translate(-.1px, 0px);
        opacity: 1;
    }
    
    80% {
        transform: scale(.9) translate(-.1px, -0px);
        opacity: 1;
    }

    100% {
        transform: scale(1) translate(-.1px, -0px);
        opacity: 0;
    }
}

@keyframes subtle-vertical-movement {
    0% {
        transform: translateY(10px)
    }

    50% {
        transform: translateY(0)
    }

    100% {
        transform: translateY(10px)
    }
}

@keyframes mild-vertical-movement {
    0% {
        transform: translateY(10px)
    }

    50% {
        transform: translateY(0)
    }

    100% {
        transform: translateY(10px)
    }
}

@keyframes radiating-scale {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    
    80% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(1.1);
        opacity: 0;
    }
}

.hypno-eye {
    svg {
        position: absolute;
        top: 0;
        left: 0;
        animation-name: hypno-eye-scale;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;

        &:nth-child(2) {
            animation-delay: 1s;
        }
    }
}

.stroke {
    .data {
        opacity: 0;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #FFEBB7;
        top: 0;
        left: 0;
        margin-top: -5px;
        margin-left: -5px;
    }
}

.confetti {
    opacity: 0;
}

.confetti-group {
    width: 500px;
    height: 500px;

    .confetti-2 {
        top: 17px;
        left: 46px;
    }

    .confetti-3 {
        top: 10px;
        left: 10px;
    }

    .confetti-4 {
        top: 30px;
        left: 2px;
    }

    .confetti-5 {
        top: 24px;
        left: 22px;
    }

    .confetti-6 {
        top: 0px;
        left: 20px;
    }

    .confetti-7 {
        top: 33px;
        left: 36px;
    }
}

.gradient-bg {
    position: absolute;
    content:'';
    background-image: url(../img/gradient-bright.jpg);
    z-index: -1;
}

/*
Desktop
>>------------->
*/

@media screen and (min-width: $breakpoint_desktop) {
    .distressed-dots-bg {
        position: absolute;
        background-image: url(../img/2x/distressed-dots-bg.png);
        width: 178px;
        height: 184px;
        background-size: 178px 184px;
        z-index: -1;
    }

    .gradient-bg {
        background-image: url(../img/gradient-bright.jpg);

        @media (min--moz-device-pixel-ratio: 1.3), (-o-min-device-pixel-ratio: 2.6/2), (-webkit-min-device-pixel-ratio: 1.3), (min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
            background-image: url(../img/2x/gradient-bright.jpg);
        }
    }
}

/*
Mobile
>>------------->
*/

@media screen and (max-width: $breakpoint_mobile) {
    .distressed-dots-bg {
        display: none;
    }

    .gradient-bg {
        background-image: url(../img/gradient-bright.jpg);
    }
}

