/*
>>================================================================================>
Global: Interactive Elements (Buttons, CTAs, Form Stuff, etc.)
>>================================================================================>
*/

/*
All Resolutions 
>>------------->
*/

.large-button {
    position: relative;
    display: block;
    height: 113px;
    width: 482px;
    margin-left: auto;
    margin-right: auto;

    .top {
        position: absolute;
        top: 0;
        left: 0;
        width: 482px;
        height: 31px;
        z-index: 2;

        svg {
            width: 482px;
            height: 31px;
        }
    }

    .label {
        background-color: #FFECB3;
        transition: background-color 150ms ease;
        position: absolute;
        height: 82px;
        bottom: 0;
        width: calc(100% - 34px);
        display: flex;
        align-items: center;
        z-index: 1;

        span {
            width: 100%;
            text-transform: uppercase;
            font-family: $font_secondary;
            font-weight: $weight_wide-bold;
            font-size: 34px;
            margin-top: 5px;
            color: #AA00FF;
            letter-spacing: 2px;
            text-align: center;
        }
    }

    &:hover {
        .label {
            background-color: #4A148C;
        }
    }

    .squiggle-animation {
        position: absolute;
        background-color: #4A148C;
        width: 34px;
        height: 113px;
        overflow: hidden;
        clip-path: polygon(100% 0, 100% 75%, 0 100%, 0 0);
        right: 0;

        .squiggles {
            position: absolute;
            width: 277px;
            height: 254px;
            top: -153px;
            left: -30px;

            img {
                width: 277px;
                height: 254px;
            }
        }
    }

    .bottom {
        position: absolute;
        width: 34px;
        height: 31px;
        bottom: 0;
        right: 0;
    }

    .blob {
        pointer-events: none;
        position: absolute;
        width: 500px;
        height: 500px;
        bottom: -175px;
        right: -0px;
        z-index: -1;

        path {
            fill: #4A148C;
        }
    }
}

