/*
>>================================================================================>
Sticky Header
>>================================================================================>
*/

#sticky-header {

    /*
    All Resolutions
    >>------------->
    */

    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;
    opacity: 0;
    transition: all 300ms ease;
    transform: translateY(-30px);

    @at-root .sticky-header-active & {
        opacity: 1;
        transform: translateY(0);
    }

    /*
    Desktop
    >>------------->
    */

    @media screen and (min-width: $breakpoint_desktop) {
        .logo {
            position: absolute;
            left: 20px;
            top: 26px;
            width: 70px;
            height: 33px;

            img {
                width: 70px;
                height: 33px;
            }
        }

        .large-button {
            position: absolute;
            right: -10px;
            top: 0;
        }

        &:after {
            content:'';
            position: absolute;
            width: 430px;
            height: 171px;
            background-image: url(../img/sticky-header-blob.png);
            top: 0;
            right: 0;
            z-index: -1;

            @media (min--moz-device-pixel-ratio: 1.3), (-o-min-device-pixel-ratio: 2.6/2), (-webkit-min-device-pixel-ratio: 1.3), (min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
                background-image: url(../img/2x/sticky-header-blob.png);
                background-size: 430px 171px;
            }
        }
    }

    /*
    Mobile
    >>------------->
    */

    @media screen and (max-width: $breakpoint_mobile) {
        .logo {
            display: none;
        }

        .button {
            position: absolute;
            right: 0;
            top: 0;
            width: 260px;
            height: 61px;
        }

        .large-button {
            transform-origin: top left;
            transform: scale(.54);
        }
    }
}

