/*
>>================================================================================>
Footer Animation
>>================================================================================>
*/

#footer-animation {

    /*
    All Resolutions
    >>------------->
    */    

    /*
    Desktop
    >>------------->
    */

    @media screen and (min-width: $breakpoint_desktop) {
        height: 270px;
        width: 100%;
        overflow: hidden;

        .arrow-container {
            display: block;
            position: relative;
            z-index: 2;
            overflow: hidden;
            height: 270px;
            width: 2560px;
            left: 50%;
            transform: translateX(-50%);
            
            &:hover {
                cursor: pointer;

                .large-button {
                    .label {
                        background-color: #4A148C;
                    }
                }
            }
        }

        .arrow-group {
            position: absolute;
            display: block;
            width: (2560px / 1.5);
            height: 270px;
        }

        .arrow-mask {
            overflow: hidden;
            width: 195px;
            height: 32px;
            top: 24px;
        }

        .arrow-squiggles {
            left: 0%;
            top: 50%;
            transform: translateY(-50%);

            .squiggles {
                width: 394px;
                height: 387px;
                top: -150px;
                left: -82px;
                transform: translate(30px,-40px);
            }
        }
        
        .arrow-container {
            .large-button {
                position: absolute;
                left: 35%;
                top: 50%;
                transform: translate(-50%, -50%);
            }   
        }

        .arrow-blobs {
            left: 72%;
            top: 50%;
            transform: translate(-100%, -50%);

            .blob {
                path {
                    fill: #E339FB;
                }
            }

            .blob-1 {
                top: -90px;
                left: 0px;
                width: 160px;
                height: 160px;
            }
    
            .blob-2 {
                top: -20px;
                left: 90px;
                width: 100px;
                height: 100px;
            }
        }

        .arrow-circles {
            right: 5%;
            top: 50%;
            transform: translateY(-50%);
            
            .circle-group-1 {
                top: 10px;
                left: 90px;
                transform: scale(.7);
            }
    
            .circle-group-2 {
                top: 25px;
                left: 140px;
                transform: scale(.7);
            }
    
            .circle {
                transform-origin: center center;
                top: 0;
                left: 10px;
                width: 10px;
                height: 10px;
                transform: rotate(0deg);
    
                path {
                    stroke: #e040fb;
                    stroke-width: 2.764;
                    stroke-miterlimit: 10;
                    fill: transparent;
                }
            }
        }
    }

    /*
    Mobile
    >>------------->
    */

    @media screen and (max-width: $breakpoint_mobile) {
        display: none;
    }
}

